.container-1 {
    max-width: 1500px;
    height: 700px;
    position: relative;
    margin:auto;
    margin-top: 4.2rem;
   

}


.images-btc {
    width: 100%;
    height: 90%;
   
}

.centered {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 80px;
    text-shadow: 2px 2px 8px #4c0ec0;
    
}


/* .container {
    max-width: 1140px;
    margin:auto;
    margin-top: 4.2rem;
    
} */

.heading {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #26272b;
    box-shadow: 0px 0px 12px #18191b;
    border-radius: 8px;
    margin: 2rem 1rem;
    padding: .7rem 1rem;
    font-weight: 700;
}

.coin-name {
    margin-left: -4rem;
}

.coin-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #26272b;
    box-shadow: 0px 0px 12px #18191b;
    border-radius: 8px;
    margin: 2rem 1rem;
    padding: .7rem 1rem;
}

.coin-row:hover {
    transform: scale(1.04);
    transition: .3s ease-in-out;
    cursor: pointer;
}

.images {
    height: 40px;
    margin-right: 8px;
    width: auto;
}

.img-symbol {
    display: flex;
    align-items: center;
}

.btn{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 8px 30px;
}

.footr1{
    display: flex;

    margin-bottom: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    
    

}

.footr2{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 8px;
}



@media screen and (max-width: 720px) {
    .hide-mobile {
        display: none;
    }
    .centered{
        font-size: 40px;
    }
}
